h1,
h2,
h3,
h4,
h5,
p,


ul,
ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}
a,
a:visited,
a:hover {
  text-decoration: none;

}
a:focus,
a:active {
  outline: none;
}
address {
  font-style: normal;
}
button {
  cursor: pointer;
}
input,
button,
textarea {
  font-family: inherit;
  font-size: inherit;
  border: none;
  outline: none;
  background: inherit;
  padding: 0;
  resize: none
}

img {
  display: block;
  min-width: 100%;
  height: auto;
  width: 100%;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
  font-style: normal;
  margin: 0;
  padding: 0;
}





